import React, { Component } from 'react'
import { Layout } from 'prey-stash'
import styled from 'styled-components'
import posed, { PoseGroup } from 'react-pose'

const ShipStyle = styled.div`
  width: 32px;
  height: 32px;
  background: red;
  position: absolute;
  left: calc(50% - 16px);
  bottom: 0;
`

const BeamStyle = styled.div`
  width: 10px;
  height: 200px;
  position: absolute;
  bottom: 0;
  left: calc(50% - 5px);
  transfrom: translateX(-50%) translateY(-100%);
  background: blue;
`

const AlienStyle = styled.div`
  width: 25px;
  height: 25px;
  background: rebeccapurple;
  position: absolute;
  top: 0; left: 0;
  transfrom: translateY(0);
`
const Stage = posed.div({
  enter: {
    opacity: 1,
    delay: 300,
    beforeChildren: true,
    staggerChildren: 30
  },
  exit: {
    opacity: 0,
    staggerChildren: 30,
  }
});

const Ship = posed(ShipStyle)({
  hoverable: true,
  draggable: 'x',
  hover: { scale: 1.2 },
  init: { scale: 1 },
  drag: { scale: 1.2 },
  dragEnd: {
    x: '50%',
    y: 0,
    transition: { type: 'spring' }
  }
})


const Aliens = posed.div({
  init: {x: 50, opacity: 0},
  enter: {
    x: 0,
    opacity: 1,
    beforeChildren: true,
    staggerChildren: 30
  },
  exit: {
    x: 50,
    opacity: 0,
    staggerChildren: 30
  }
});

const Alien = posed.div({
  init: {y: 0, opacity: 0},
  enter: { y: 500, opacity: 1 },
  exit: { y: 0, opacity: 0 }
});

class Camo extends Component {


  componentDidMount = (e) => {
    console.log('start pew pew')
    console.log('this rect ', this.refs.hero.getBoundingClientRect());
    console.log('element ', this.refs.alien.getBoundingClientRect());
  }
  onStart = (e) => {
    // console.log('dragged to ', e);
  }
  render() {
    return (
      <Layout scrollableFix={true} className="camo">
        <h1>Game On!</h1>
        <Stage>
          <Aliens ref="aliens" className="aliens">
            <Alien ref="alien" />
          </Aliens>
          <Ship
            key="hero"
            ref="hero"
            onDragStart={this.onStart}
            >
            <BeamStyle />
          </Ship>
        </Stage>
      </Layout>
    );
  }
}

export default Camo;
